 }  // background-image: url("../img/back.svg")

canvas {
  display: block; }


@import url('https://fonts.googleapis.com/css?family=Amatic+SC');

#loading-screen {
  height: 100vh;
  width: 100vw;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-flow: column nowrap;
  font-family: 'Amatic SC', cursive;
  font-size: 25px;
  background: #000000;
  color: #ffffff;
  position: relative;
  z-index: 1; }

#loading-icon {
  position: absolute;
  z-index: 1; }
.mango-firstpage {}
.mango-wrap {}
.header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 100px 0 0; }
.menuitems {
  display: flex;
  align-items: flex-end;
  ul {
    display: flex;
    li {
      img {
        padding-right: 10px; } } } }
.menuicon {
  display: flex; }
.items {
  color: #ffff;
  padding: 0 130px 0px 0;
  font-family: "Rubik";
  font-style: normal;
  font-weight: 300;
  font-size: 18px;
  line-height: 21px;
  &:hover {
    transition: .3s ease-in-out;
    transition-timing-function: ease-in-out;
    font-weight: bold;
    letter-spacing: 2px; } }
